import Counter from "../../../components/counter.vue"
export default {
	components: {
		Counter
	},
	data() {
		return {
			theIndex: null, //控制滑动效果 当前滑动下标
			oldIndex: null, //上一个左滑下表
			goodsList: [],
			isSelectedAll: false,
			selectedList: [],
			sumPrice: '0.00'
		}
	},
	methods: {
		initData() {
			uni.showLoading({
				title: "加载中..."
			})
			uni.getStorage({
				key: "goodsList",
				success: res => {
					for (let i = 0; i < res.data.length; i++) {
						res.data[i].selected = false;
					}
					this.goodsList = res.data;
				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		changeStorage(goods) {
			uni.getStorage({
				key: "goodsList",
				success: res => {
					let goodsList = res.data;
					goodsList.some(item => {
						if (item.goods_id == goods.goods_id) {
							item.number = goods.number;
							return;
						}
					})

					uni.setStorage({
						key: "goodsList",
						data: goodsList
					})
				}
			})
		},
		add(goods) {
			goods.number++;
			this.changeStorage(goods);
			this.sum();
		},
		sub(goods) {
			if (goods.number <= 1) {
				return;
			}
			goods.number--;
			this.changeStorage(goods);
			this.sum();
		},
		handleCheckbox(goods) {
			goods.selected = !goods.selected;

			let isExist = this.selectedList.indexOf(goods);
			if (isExist > -1) {
				this.selectedList.splice(isExist, 1);
			} else {
				this.selectedList.push(goods)
			}
			
			if (this.goodsList.length == this.selectedList.length) {
				this.isSelectedAll = true;
			} else {
				this.isSelectedAll = false;
			}
			this.sum();
		},
		selectAll() {
			this.isSelectedAll = !this.isSelectedAll

			let arr = [];
			this.goodsList.forEach(goods => {
				goods.selected = this.isSelectedAll;
				arr.push(goods);
			})

			this.selectedList = this.isSelectedAll ? arr : [];
			this.sum();
		},
		handleSingleDelete(goods) {
			this.goodsList.splice(this.goodsList.indexOf(goods), 1);
			
			let isExist = this.selectedList.indexOf(goods);
			if (isExist > -1) {
				this.selectedList.splice(isExist, 1);
			}
			
			uni.setStorage({
				key: "goodsList",
				data: this.goodsList
			})
		},
		handleGoodsInfo(goods) {
			uni.navigateTo({
				url: "../../goods/goods?goodsInfo=" + JSON.stringify(goods)
			})
		},
		handleTouchStart(index, event) {
			if (event.touches.length > 1) {
				return;
			}

			this.isFirstMove = true;
			this.oldIndex = this.theIndex;
			this.theIndex = null;

			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		handleTouchMove(index, event) {
			if (event.touches.length > 1) {
				return;
			}
			if (this.isFirstMove) {
				this.isFirstMove = false;
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];

				if (Math.abs(moveX) < 5) {
					return;
				}

				if (Math.abs(moveY) > Math.abs(moveX)) {
					return;
				}
				if (moveX < 0) {
					this.theIndex = index;
				}

			}
		},
		handleTouchEnd(index, event) {
			if (event.touches.length > 1) {
				return;
			}
		},
		sum() {
			this.sumPrice = 0;
			this.goodsList.forEach(goods => {
				if (goods.selected) {
					this.sumPrice = this.sumPrice + (goods.number * goods.price);
				}
			})
			this.sumPrice = this.sumPrice.toFixed(2);
		}
	},
	onShow() {
		this.isSelectedAll = false;
		this.selectedList = [];
		this.sumPrice = '0.00';
		this.initData()
	}
}
